  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  height: 100vh;
  height: 100dvh;
}

.pick-list{
  display: flex;
  flex-direction: column;
  gap: 4px;
  max-height: calc(100vh - 192px);
  max-height: calc(100dvh - 192px);
