}

.chat-message {
  //color: aliceblue !important;
  margin-bottom: 0 !important;
}

.mu-text-field-input {
  color: white !important;
}

.chatbox-wrapper{
  display: flex;
  padding: 20px;
  gap: 16px;
}

.chat-quick-bar {
  padding: 10px 0;
  color: #009688;
  font-size: 14px;

  span {
    cursor: pointer;
    display: inline-block;
    padding: 0 1px;
  }
}

#blur {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0.3;
  z-index: -99;
  overflow: hidden;
  filter: blur(30px);
}

