}

@media screen and (max-width: 768px) {
  .playing-music,
  .playing-music-info,
  .playing-status {
    max-width: 100%;
  }
  
  .mobile-bottom-jump{
    display: flex;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 64px;
    color: white;
    z-index: 999;
    cursor: pointer;
    align-items: center;
    justify-content: center;
    box-shadow: 0 0 8px #00000080;
  }

  .container-fluid.demo-container {
    margin: 72px 0 64px;
    height: calc(100vh - 136px);
    height: calc(100dvh - 136px);
  }
}